<template>
    <div id = '控制台' :style = 'div_css'>
      <t-divider class = 'divider'>
        Console
      </t-divider>
      <t-button v-for = '(d,i) in button_name'
      :key = 'i'
      :id = "'按钮' + (i+1)"
      class = '按钮'
      shape = 'round'
      :theme="mode == d?'primary':'default'"
      @click = 'mode = d'>
      {{d}}
      </t-button>
     
      <t-slider class = '滑块'
      :marks='marks'/>
    </div>
</template>

<script>

export default {
  name: 'controller',
  props: {
    //这两个属性从父组件继承
    height:Number,
    width:Number,
    top:Number,
    left:Number,
  },
  data(){
    return {
      button_name:['Demographics','Social Network','Business Base'],
      mode:'Demographics',
      marks :{'0':'min:0',100: 'max:100'},
    }
  },
  mounted(){

  },
  computed:{
    div_css(){
      //div的css属性从这里写
      let div_height = this.height
      let div_width = this.width
      let div_border = '0px solid black'
      let div_position = 'absolute'
      let div_top = this.top //所有边距固定为10
      let div_left = this.left

      return  "position:" + div_position + ";" +
              "height:" + div_height + "px;" +
              "width:" + div_width + "px;" +
              "border:" + div_border + ";" +
              "left:" + div_left + "px;" +
              "top:" + div_top + "px;" 
              


    }
  },
  methods:{

  },
  watch:{

  }
}
</script>

